.root {
    margin-top: 20px;
    background-color: white;
}
.btn {
    display: inline-block;
    height: 50px;
    margin: 10px;
    line-height: 50px;
    font-size: 18px;
    text-align: center;
    cursor: pointer;
    border-radius: 25px;
}
.cancel {
    flex: 1;
    background-color: #fff;
    border: 1px solid #0c9785;
    color: #0c9785;
}
.ok {
    flex: 1;
    background-color:  #0d9b88;
    border: 1px solid #d4d1d1;
    color: #fff;
}