.root {
    position: fixed;
    top: 0;
    right: 0;
    width: 80vw;
    height: 100vh;
    padding-top: 14px;
    padding-bottom: 50px;
    border-left: 1px solid #008e82;
    background-color: #ffffff;
    z-index: 1;
}

p {
    margin-bottom: 10px;
    font-size: 20px;
    color: #008b5d;
    text-align: center;
    line-height: 30px;
}

.tags {
    overflow: scroll;
    padding: 10px;
    height: 85%;
}

.name {
    height: 35px;
    padding-left: 20px;
    color: #008f77;
    font-size: 18px;
    line-height: 35px;
}

.child {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    border-bottom: 1px solid #64b99f;
}

.tag {
    display: inline-block;
    width: 80px;
    height: 35px;
    margin: 8px 6px;
    border: 1px solid #0c9785;
    background-color: #fff;
    color: #333;
    text-align: center;
    line-height: 35px;
    font-size: 14px;
    border-radius: 6px;
}

.tagActive {
    background-color: #ddfff2;
    color: #008b5d;
    border: 1px solid #008b5d;
}

.buttons {
    position: absolute;
    width: 100%;
    bottom: 50px;
    right: 0;
}